<script>
  	import { swiper, swiperSlide } from "vue-awesome-swiper";
  	require("swiper/dist/css/swiper.css");
  	import { mapGetters, mapActions, mapMutations } from 'vuex';
	import {ADD_ITEM} from '../../../store/types.js';
export default {
	data(){
		const that = this;
		return{
			class1:"m-actionsheet",
			mun:1,
			mid:"",
			list:{},
			imgIndex: 1,
			swiperOption: {
        		//notNextTicktrueNextTickswiperswiperswipertrue
        		notNextTick: true,
        		//
        		loop: true,
        		//slide
        		initialSlide: 0,
        		//
        		autoplay: {
          			delay: 1500,
          			stopOnLastSlide: false,
          			/*  */
         	 		disableOnInteraction: false
        		},
       			 //
        		speed: 800,
        		//
        		direction: "horizontal",
        		//
        		grabCursor: true,
        		on: {
          			//
          			slideChangeTransitionStart: function() {
            			/* realIndexslide */
            			that.imgIndex= this.realIndex - 1;
          			},
        		},
        		//
        		pagination: {
          			el: ".swiper-pagination",
          			clickable: true,
          			type: "bullets"
        		}
			}
		}
	},
	components:{
		  	swiper,
  			swiperSlide
	},
	methods:{
		jia(){
			this.mun++
		},
		jian(){
			this.mun--
		},
		buycar(){
			
			if(this.mid){
				axios({
					url:"/api/buycarg",
					params:{mid:this.mid,sid:this.id,dataName:this.dataName,num:this.mun}
				}).then(
					res=>this.list = res.data
					
				)
			}else{
				alert("")
				this.$router.push("/login")
			}
			console.log(this.list)
		}
	},
	props:['detail','id','dataName'],
	mounted(){
		this.mid = localStorage.getItem("mid")
	},
	destroyed(){
		if(this.mid){
			this.$store.dispatch({type:ADD_ITEM,mid:this.mid});
		}else{
			return
		}
		
	}
}
</script>
