<script>
import {mapGetters,mapActions} from 'vuex'
import { setTimeout } from 'timers';
export default {
	data(){
		return {
			mid:"",
			zs:0,
			zj:0,
			c:[],
			list:[]
		}
	},
	computed:mapGetters([
    	'buycar'
  	]),
	created(){
		let a = this.$route.params.id.split(":")[1]
		let b = a.split(",")
		for (let d = 0;d<b.length;d++){
			if(b[d]){
				this.c.push(b[d])
			}
		}
		console.log(this.c)
	},
	mounted(){
		console.log(this.buycar)
		for(let i = 0;i<this.c.length;i++){
			for(let j = 0;j<this.buycar.length;j++){
				if(this.c[i]===this.buycar[j]._id){
					this.list.push(this.buycar[j])
				}
			}
		}
		
				let mzs = 0;
				let mzj = 0;
				for(let a = 0; a<this.list.length;a++){
					mzs += (this.list[a].num)*1;
					var j = this.list[a].spsj.data.PresentP
					// mzj += ((`${this.buycar[a].spsj.data.PresentP}`).replace(//,""))*((this.buycar[a].num)*1)
					// console.log(j.substring(1,j.length))
					mzj += j.substring(1,j.length)*((this.list[a].num)*1)
				}
				this.zs = mzs;
				this.zj = mzj;
				console.log(this.list,this.zs,this.zj)
	},
	methods: {
		tjdd(){
			this.$router.push(`/settlement/${this.zj}`)
		},
		addres(){
			this.$router.push(`/myaddres`)
		}
	},
}
</script>
